html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }

p {
  font-size: 2rem;
  color: #000; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1 {
  font-size: 4rem;
  color: 900; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

h1, h2, h3 {
  margin: 0; }

.bg-amarillo {
  background-color: #F3F46D; }

.bg-verde {
  background-color: #4cb8b3; }

.header {
  background-color: #4cb8b3;
  transition: position .5s ease-in-out; }
  .header .contenido-header {
    padding: 2.5rem 0; }
    @media (min-width: 768px) {
      .header .contenido-header {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: #fff;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 768px) {
      .header h1 {
        margin-bottom: 0; } }
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0 .3rem .4rem #444;
    z-index: 999; }

.navegacion-principal {
  display: flex;
  gap: 2rem;
  justify-content: center; }
  .navegacion-principal a {
    color: #fff;
    font-size: 2rem; }
    .navegacion-principal a:hover {
      color: #F3F46D; }

.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e3f92+77,ffffff+100&0.8+0,0.5+100 */
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 63, 146, 0.57) 77%, rgba(255, 255, 255, 0.5) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 63, 146, 0.57) 77%, rgba(255, 255, 255, 0.5) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 63, 146, 0.57) 77%, rgba(255, 255, 255, 0.5) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#cc891c1e', endColorstr='#80ffffff', GradientType=1);
    /* IE6-9 fallback on horizontal gradient */ }
  .video video {
    display: block;
    width: 100%; }

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%; }
  .contenido-video h2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #fff;
    font-size: 7rem; }
    .contenido-video h2 span {
      background-color: #f53756;
      padding: 1rem 2rem;
      font-size: 3rem; }

.sobre-festival {
  padding: 5rem 0; }
  @media (min-width: 768px) {
    .sobre-festival {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 1rem; } }

.contenido-festival {
  padding-top: 1rem; }
  .contenido-festival .fecha {
    color: #f53756;
    font-weight: 700; }
  .contenido-festival .texto {
    line-height: 2; }

.lineup {
  background-color: #f53756;
  padding: 5rem 0; }
  .lineup h3,
  .lineup p {
    text-align: center;
    color: #fff; }
  .lineup .dia {
    text-transform: uppercase;
    margin: 5rem 0; }

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem; } }

.escenarios-contenedor .nombre-escenario {
  color: #000;
  font-size: 2rem;
  font-weight: 300; }
  .escenarios-contenedor .nombre-escenario:after {
    content: '';
    width: 20rem;
    height: 10rem;
    display: block;
    margin: 2rem auto 0 auto;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center; }

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png); }

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png); }

.calendario {
  list-style: none;
  padding: 0; }
  .calendario li {
    padding: 2.5rem;
    font-weight: 700; }
    .calendario li span {
      font-weight: 400; }

.bg-verde .calendario li:nth-child(odd) {
  background-color: #3b9691; }

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #eff03e; }

.galeria {
  padding: 5rem 0; }
  .galeria h3 {
    text-align: center; }

.galeria-imagenes {
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem; }
  @media (min-width: 768px) {
    .galeria-imagenes {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 1rem; } }
  @media (min-width: 1200px) {
    .galeria-imagenes {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      column-gap: 1rem; } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 10;
  display: grid;
  place-items: center; }
  .overlay .img-overlay {
    max-width: 100%;
    height: 70%; }

.btn-cerrar {
  width: 1rem;
  height: 1rem;
  margin: 0;
  background-color: #fff;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 3rem;
  border-radius: 50%; }
  .btn-cerrar::hover {
    cursor: pointer; }

.boletos {
  padding: 5rem 0; }
  .boletos h3 {
    text-align: center;
    color: #fff; }
  .boletos .contenedor-boletos {
    margin-top: 2.5;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem; }
  .boletos .plan p {
    color: #fff;
    font-size: 3rem;
    text-align: center;
    margin-bottom: 2rem; }

.pase {
  width: 100%;
  border-spacing: 0; }
  .pase thead {
    background-color: #f53756; }
    .pase thead th {
      padding: 1.25rem;
      color: #fff;
      text-transform: uppercase;
      font-weight: 300;
      font-size: 2rem; }
  .pase tbody {
    background-color: #fff; }
    .pase tbody td {
      padding: 1.25rem; }
  .pase tfoot {
    background-color: #752F77; }
    .pase tfoot td {
      padding: 1.25rem;
      color: #fff;
      text-align: center;
      font-weight: 400; }

footer {
  background-color: #f53756;
  text-align: center;
  padding: 2rem; }
  footer p {
    margin: 0;
    color: #fff; }
